<template>
  <div class="container">
    <form
      @submit.prevent="onSubmit"
      action="#"
      class="field"
    >
      <div>
        <div
          class="form-item"
          :class="{ errorInput: $v.name.$error }"
        >
          <label for="#"> </label>
          <br>
          <input
            v-model="name"
            type="text"
            :class="{ 'is-danger error': $v.name.$error }"
            class="input is-primary input-text"
            @blur="$v.name.$touch()"
            placeholder="  "
          >
          <div
            class="error"
            v-if="!$v.name.required"
          >
            <p> ,   !</p>
          </div>
        </div>
        <div
          class="form-item"
          :class="{ errorInput: $v.phone.$error }"
        >
          <label for="#">  </label>
          <br>
          <masked-input
            :class="{ 'is-danger error': $v.phone.$error }"
            v-model="phone"
            type="tel"
            mask="\+\996 (111) 11-11-11"
            class="input is-primary input-text"
            placeholder="+996 (___)__-__-__"
            @blur="$v.phone.$touch()"
          />
          <div
            class="error"
            v-if="!$v.phone.required"
          >,    !</div>
        </div>
        <div
          class="form-item"
          :class="{ errorInput: $v.address.$error }"
        >
          <label for="#"> </label>
          <br>
          <input
            :class="{ 'is-danger error': $v.address.$error }"
            @blur="$v.address.$touch()"
            v-model="address"
            type="text"
            class="input is-primary input-text"
            placeholder=".  89 . . "
          >
          <div
            class="error"
            v-if="!$v.address.required"
          >,    !</div>
        </div>
      </div>
      <strong>
         :</strong>
      <ul>
        <li
          v-for="(product, index) in getProductsInCart"
          :key="index"
        >{{product.name}} x {{product.quantity}} = {{product.quantity*product.price}} </li>
      </ul>
      <strong>  : {{totalPrice}} </strong>
      <div class="buttons">
        <button
          type="submit"
          class="button is-success"
          :disabled="(submitStatus === 'PENDING')"
        > </button>
        <router-link
          to="/cart"
          class="button is-danger"
        > </router-link>
      </div>
    </form>
    <p
      class="typo__p"
      v-if="submitStatus === 'OK'"
    >  ,   !</p>
    <p
      class="error"
      v-if="submitStatus === 'ERROR'"
    >   !</p>
    <p
      class="typo__p"
      v-if="submitStatus === 'PENDING'"
    >...</p>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
//validation controlls all 3 inputs to be required.
import { required } from 'vuelidate/lib/validators'
//validation controlls phone number input.
import MaskedInput from 'vue-masked-input'

export default {
  data () {
    return {
      name: '',
      phone: '',
      address: '',
      submitStatus: null,
      product: {
        quantity: 0
      }
    }
  },
  validations: {
    name: {},
    phone: { required },
    address: { required }
  },
  computed: {
    ...mapGetters(['getProductsInCart']),
    //all cost.
    totalPrice () {
      return this.getProductsInCart.reduce((total, next) =>
        total + (next.quantity * next.price), 0);
    }
  },
  components: {
    //used for phone number validation.
    MaskedInput
  },
  methods: {
    //submit button validation.
    onSubmit () {
      this.$v.$touch()
      if (this.$v.$invalid) {
        this.submitStatus = 'ERROR'
      } else {
        const orderCart = {
          customer_name: this.name,
          phone: this.phone,
          address: this.address,
          orderList: this.getProductsInCart.map(element => {
            return {
              product_id: element._id,
              product_quantity: element.quantity
            }
          }),
          total_price: this.totalPrice
        }
        console.log(orderCart);
        this.submitStatus = 'PENDING'
        this.$store.dispatch("addOrder", orderCart);
        this.submitStatus = 'OK'
        this.$toast.open({
          message: `  ! `,
          duration: 3000,
          position: 'is-top',
          type: 'is-dark'
        });

      }
    }
  }
}
</script>
<style scoped>
.container {
  margin-top: 2em;
  margin-left: 3%;
  margin-right: 3%;
}
.field {
  width: 100%;
}
.buttons {
  margin-top: 1em;
}
.error {
  color: #ff3860;
}
.form-item > .error {
  display: none;
}
.form-item.errorInput > .error {
  display: block;
}
</style>
